import {
    ComponentRef
} from '@angular/core';
import { TaskList } from './taskList';

export class ToolBar {
    protected toolBarView: ToolBarView;
    protected toolBarId: string;
    protected data: any;
    get id(): string {
        return this.toolBarId;
    }
    set id(value: string) {
        this.toolBarId = value;
    }
    constructor(protected taskList: TaskList) {
        // this.toolBarId = workspace.util.newGuid();
    }
    activate(state) {
        this.toolBarView = new ToolBarView(state);
    }
}

export class ToolBarView {
    protected element: ComponentRef<any>;

    constructor(serializedState: any) {
    }

    getElement(): ComponentRef<any> {
        return this.element;
    }

    serialize(): void {
    }

}

export class ToolBarModule {
    protected toolbar: ToolBar;
    constructor(protected taskList: TaskList) {

    }

    getPackage() {
        if (!this.toolbar) {
            this.initPackage();
        }
        return this.toolbar;
    }

    protected initPackage() {
        this.toolbar = new ToolBar(this.taskList);
    }
}



