import { Injector, ComponentFactoryResolver } from '@angular/core';
import { ToolBar, ToolBarView } from '../taskcenter/toolbar';
import { TodoToolbarComponent } from './todo-toolbar/todo-toolbar.component';
import { TaskList } from '../taskcenter/taskList';

export class TodoToolBar extends ToolBar {
    constructor(taskList: TaskList, private cfr: ComponentFactoryResolver,
        private injector: Injector) {
        super(taskList);
    }
    activate(state) {
        if (!this.toolBarView) {
            this.toolBarView = new TodoToolBarView(state, this.cfr, this.injector);
            // 通过设置构件引用展示View
            this.taskList.setCompoment(this.toolBarView.getElement());

            const currentElement = this.toolBarView.getElement().instance as TodoToolbarComponent;
            // TODO 传递参数
            if (currentElement) {
                // currentElement.databaseObject = this.data;
            }
        }
        // this.taskList.activePackage(this);
    }
}

export class TodoToolBarView extends ToolBarView {
    constructor(serializedState: any,
        private cfr: ComponentFactoryResolver,
        private injector: Injector) {
        super(serializedState);
        const componentFactory = this.cfr.resolveComponentFactory(TodoToolbarComponent);
        this.element = componentFactory.create(this.injector);
    }
}



