import {
  Component, OnInit, Input, ViewChild,
  ViewContainerRef, Output, EventEmitter
} from '@angular/core';

@Component({
  selector: 'app-todo-toolbar',
  templateUrl: './todo-toolbar.component.html',
  styleUrls: ['./todo-toolbar.component.css']
})
export class TodoToolbarComponent implements OnInit {

  _bizTypeId: string;
  enable = true;
  @Input()
  set bizTypeId(name: string) {
    this._bizTypeId = name;
  }
  get bizTypeId(): string {
    return this._bizTypeId;
  }
  @Output() btnClickEvt = new EventEmitter<number>();
  // @ViewChild('toolbarContainer', { read: ViewContainerRef })
  // container: ViewContainerRef;
  constructor() { }

  ngOnInit() {
  }
  onApproveClick(v: number): void {
    this.btnClickEvt.emit(v);
  }
  // 回调刷新列表
  onRefresh(): void {
    alert('onRefresh');
  }
}



